* {
    box-sizing: border-box;
}
.wrapper {
    width: 100%;
    max-width: 1000px;
    margin: 0em auto;
    padding: 1em;
}
.is-striped {
    background-color: rgba(233, 200, 147, 0.2);
}
/* Table column sizing ================================== */
.date-cell {
    width: 15%;
    
}
.topic-cell {
    width: 30%;
}
.access-link-cell {
    width: 15%;
    align-items: center;
    display: flex;

}
.replay-link-cell {
    width: 40%;
}

/* Apply styles ================================== */
.Rtable {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 3em 0;
    padding: 0;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
}
.Rtable .Rtable-row {
    width: 100%;
    border-bottom: 0.5px solid lightgray;
    display: flex;
}
.Rtable .Rtable-row .Rtable-cell {
    box-sizing: border-box;
    flex-grow: 1;
    padding: 0.8em 1.2em;
    overflow: hidden;
    list-style: none;
    display: flex;
    align-items: center;
}
.Rtable .Rtable-row .Rtable-cell.column-heading {
    background-color: #43bac0;
    color: white;
    padding: 1em;
}
.Rtable .Rtable-row .Rtable-cell .Rtable-cell--heading {
    display: none;
}
.Rtable .Rtable-row .Rtable-cell .Rtable-cell--content a {
    font-size: 12px;
    text-decoration: none;
    display: flex;
    color: gray;
    align-items: center;
}

.font-12{
    font-size: 12px;
    font-style: italic;
}

.color-grey{
    color: grey;
}
.access-link-content span{
    padding-left:10px ;
}
/*  */
.replay-link-content{
    display: flex;
    width: 100%;
    align-items: center;
}

.replay-link-content span,.access-link-content span{
    padding-left:10px ;
}

.Rtable-cell--image{
    width: 40px;
    height: 40px;
    margin-right: 10px;
}
.Rtable img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 5px;
}

.title-content{
    display: flex;
    flex-direction: column;
}
.access-link-content{
    display: flex;
    align-items: center;
}

.replay-link-content a{
    width:33%
}

/*  */

.Rtable .Rtable-row .Rtable-cell .Rtable-cell--content img{
    width: 25px;
    height: 25px;
}
/* .Rtable .Rtable-row .Rtable-cell .Rtable-cell--content .webinar-date {
    font-weight: 700;
} */
/* Responsive ==================================== */
@media all and (max-width: 750px) {
    .is-striped {
        background-color: white;
   }
    .Rtable--collapse {
        display: block;
        width: 100%;
        padding: 1em;
        box-shadow: none;
   }
    .Rtable--collapse .Rtable-row {
        box-sizing: border-box;
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 2em;
        box-shadow: 0 0 40px rgba(0, 0, 0, 0.2);
   }
    .Rtable--collapse .Rtable-row .Rtable-cell {
        width: 100% !important;
        display: flex;
        align-items: center;
   }
    .Rtable--collapse .Rtable-row .Rtable-cell .Rtable-cell--heading {
        display: inline-block;
        flex: 1;
        max-width: 120px;
        min-width: 120px;
        color: #43bac0;
        font-weight: 700;
        border-right: 1px solid #ccc;
        margin-right: 1em;
   }
    .Rtable--collapse .Rtable-row .Rtable-cell .Rtable-cell--content {
        flex: 2;
        display: flex;
        flex-direction: column;
        padding-left: 1em;
   }
    .Rtable--collapse .topic-cell {
        background-color: #43bac0;
        color: white;
        font-weight: 700;
        order: -1;
   }
   .Rtable .Rtable-row .Rtable-cell .Rtable-cell--content a{
       width: 100%;
   }
    .Rtable--collapse .topic-cell .Rtable-cell--content {
        padding-left: 0 !important;
   }
    .Rtable--collapse .Rtable-row--head {
        display: none;
   }

   .replay-link-content{
       display: flex;
       flex-direction: row !important;
   }

   .replay-link-content a span{
       display: none;
   }
}
.no-flexbox .Rtable {
    display: block;
}
.no-flexbox .Rtable.Rtable-cell {
    width: 100%;
}


/* tabs */

.tab-pills{
    display: flex;
    border-bottom: 0.5px solid lightgray;
}

.tab-pills .tab-Pill{
position: relative;
padding: 10px;
}

.tab-pills .tab-Pill.active:after{
        content: "";
        width: 100%;
        height: 5px;
        bottom: 0px;
        background: #c1d896;
        top: 10;
        position: absolute;
        left: 0;
}

/* header */

.headerBar{
   background: #1f2640;}

   .date-content {
    flex-direction: column !important;
    display: flex;
}